form {
    display: flex;
    flex-direction: column;
    padding: 0 20px;
    max-width: 400px;
    margin: 50px auto 0 auto;
}

.input {
    outline: none;
    border: none;
    border: 1px solid rgb(51, 51, 51);
    border-radius: 0px;
    height: 40px;
    padding: 0 0 0 20px;
    margin: 0 0 20px 0;
    transition: all 0.3s;
}

.input:focus {
    border: 1px solid rgb(51, 51, 51);
}

.input:hover {
    border: 1px solid rgb(51, 51, 51);
}

.submit-button {
    height: 40px;
    background: transparent;
    border: 1px solid rgb(0, 0, 0, 0.3);
    transition: all 0.3s;
    cursor: pointer;
    color: rgb(51, 51, 51);
}

.submit-button:hover {
    background: rgb(51, 51, 51);
    color: rgb(51, 51, 51);
}

.error {
    color: red;
    font-size: 12px;
}