body {
  margin: 0;
}
.App {
  text-align: center;
  margin: 0;
  padding: 0;
  background: #fff;
}

* {
  font-family: 'Rubik', sans-serif;
  font-weight: 300;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
.card-loader {
    width:100%;
    height: 400px;
}
.main {
    width: 100%;
    display: flex;
}

.main-products-block {}

.products {
    width: 100%;
    margin: 0 auto;
}

.list-items {
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 20px;
    grid-row-gap: 20px;
}

@media (max-width:1100px) {
    .list-items {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (max-width:767px) {
    .list-items {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width:640px) {
    .list-items {
        grid-template-columns: repeat(1, 1fr);
    }
}

.cart-item-image {
    width: 50px;
    height: 50px;
    margin-right: 10px;
}


.filters {
    margin: 20px 0;
    display: flex;

}

.filters select {
    align-self: flex-start;
    border: none;
    outline: none;
    color: #FFF;
    cursor: pointer;
    background: rgb(51, 51, 51);
    padding: 5px 10px;
}

.filters select option:hover {
    color: #515151;
    background: #fff;
}

.main-filters-block {
    min-width: 200px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    border: 1px solid #515151;
    margin: 67px 20px 20px 20px;
    padding: 20px;
}

.categories-list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.list-item {
    list-style: none;
    border: 1px solid rgb(51, 51, 51);
    display: flex;
    flex-direction: column;
    flex: 1 1;
    padding: 10px;
}

.item-title {
    margin-top: 20px;
    flex: 1 1;
    text-align: center;
    padding: 0 20px;
    color: rgb(51, 51, 51);
    font-weight: 300;
    font-size: 14px;
    text-decoration: none;
}

.item-title:hover {
    text-decoration: underline;
}


.item-image-block {
    padding: 20px;
    width: 80%;
    height: 50%;
    align-self: center;
}

.item-image {
    width: 100%;
    height: 100%;
    max-width: 200px;
    max-height: 250px;
}

.add-to-cart-button {
    border: none;
    background: rgb(51, 51, 51);
    border: 1px solid rgb(51, 51, 51);
    color: #fff;
    padding: 10px 0;
    transition: all 0.3s;
    cursor: pointer;
}

.add-to-cart-button:hover {
    background: transparent;
    border: 1px solid rgb(51, 51, 51);
    color: rgb(51, 51, 51);
}

.item-rating {
    align-self: center;
    width: 100%;
    display: flex;
    justify-content: center;
}

.rating-star {
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 15px;
    height: 15px;
}

.item-price {
    align-self: center;
    margin: 10px 0;
    font-weight: 400;
    color: rgb(51, 51, 51);
    font-size: 18px;
    text-decoration: none;
}

.item-price:hover {
    text-decoration: underline;
}
.header {
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid rgb(51, 51, 51);
}

.logo {
    margin-left: 50px;
}

.cart {
    margin-right: 50px;
}

.nav ul,
.login-nav {
    display: flex;
    list-style: none;
    grid-gap: 20px;
    gap: 20px;
}

.nav ul li a,
.login-nav li a {
    position: relative;
    text-decoration: none;
    color: rgb(51, 51, 51);
    text-transform: uppercase;
}

.nav ul li a::before,
.login-nav li a::before {
    position: absolute;
    content: "";
    height: 1px;
    width: 0%;
    left: 0;
    bottom: -2px;
    background-color: rgb(51, 51, 51);
    transition: all 0.3s;
}

.nav ul li a:hover,
.login-nav li a:hover {
    color: rgb(51, 51, 51);
}

.nav ul li a:hover::before,
.login-nav li a:hover::before {
    width: 100%;
    color: rgb(51, 51, 51);
}

.logo {
    background-image: url(../../static/media/logo.9055ff8d.svg);
    background-position: center;
    background-repeat: no-repeat;
    width: 50px;
    height: 50px;
}
.empty-cart {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    grid-gap:20px;
    gap:20px;
    font-size:1.2rem;
    font-weight: 500;
}

.empty-cart img {
    width:40px;
}
.cart {
    position: relative;
}

.cart-icon {
    cursor: pointer;
    background-image: url(../../static/media/cart-icon.45d95a4b.svg);
    background-repeat: no-repeat;
    background-position: center;
    width: 25px;
    height: 25px;
}

.cart-counter {
    font-size: 10px;
    color: #fff;
    content: "";
    position: absolute;
    right: -10px;
    top: -5px;
    width: 15px;
    height: 15px;
    background: rgb(51, 51, 51);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.cart-bar {
    position: absolute;
    width: 400px;
    border: none;
    background-color: rgb(51, 51, 51);
    top: 40px;
    left: -390px;
    display: flex;
    padding: 20px 10px;
    flex-direction: column;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
    height: auto;
    z-index: 1;
    transition: all 0.3s;
}

.cart-bar.visible {
    color: #fff;
    border: 1px solid #1f1f1f;
    display: flex;
    -webkit-animation: show-up 0.3s;
            animation: show-up 0.3s;
    visibility: visible;
    transition: all 0.3s;
    opacity: 1;
}

@-webkit-keyframes show-up {
    from {
        -webkit-transform: translateY(40px);
                transform: translateY(40px);
    }

    to {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}

@keyframes show-up {
    from {
        -webkit-transform: translateY(40px);
                transform: translateY(40px);
    }

    to {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}

.hide-cart-bar {
    visibility: hidden;
    /* animation: hide-up 0.3s; */
    opacity: 0;
}

