.cart-items-list {
    width: 100%;
    list-style: none;
    padding: 0;
    margin: 0;
    padding: 5px;
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
}

.cart-scroll {
    max-height: 300px;
    overflow-y: scroll;
}

.cart-item {
    font-size: 0.8rem;
    height: 40px;
    background: rgb(51, 51, 51);
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: all 0.3s;
    border: 1px solid #fff;
}

.cart-item:hover {
    background: #1f1f1f;
}

.product-title {
    width: 80%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: clip;
    flex: 1 1;
    margin-right: 10px;
}

.product-price {
    margin-right: 10px;
    margin-left: 10px;
    flex: 1 1;
}

.empty-cart,
.clear-cart,
.cart-summ {
    align-self: center;
}

.cart-summ {
    align-self: flex-end;
}

.cart-delete-button {
    background-color: transparent;
    border: 1px solid #fff;
    padding: 5px 10px;
    cursor: pointer;
    color: #fff;
    transition: all 0.3s;
}

.cart-delete-button:hover {
    background-color: #fff;
    color: rgb(51, 51, 51);
    border: 1px solid #fff;
}

.cart-nav {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    grid-gap: 20px;
    gap: 20px;
}

.clear-cart,
.confirm-order {
    border: none;
    background-color: transparent;
    cursor: pointer;
}

.clear-cart {
    text-decoration: underline;
    color: #fff;
}

.clear-cart:hover {
    text-decoration: none;
}

.confirm-order {
    padding: 5px 10px;
    border: 1px solid #fff;
    background-color: rgb(51, 51, 51);
    color: #fff;
    transition: all 0.3s;
}

.confirm-order:hover {
    background-color: #fff;
    color: rgb(51, 51, 51);
}

.item-counter {
    display: flex;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    flex: 1 1;
}

.item-counter button {
    width: 20px;
    height: 20px;
    cursor: pointer;
    border: 1px solid #fff;
    color: #fff;
    background-color: transparent;
    flex: 1 1;
    transition: all 0.3s;
}

.item-counter button:hover {
    background: #fff;
    color: rgb(51, 51, 51);
}

.promo {
    align-self: flex-end;
    display: flex;
}

.promo input {
    background: rgb(255, 255, 255, 0.5);
    height: 40px;
    border: none;
    outline: none;
    margin: 0;
    padding: 0;
    color: #fff;
    width: 200px;
    padding-left: 10px;
}

.promo input::-webkit-input-placeholder {
    color: #fff;
}

.promo input::placeholder {
    color: #fff;
}

.promo-button {
    border: none;
    height: 40px;
    cursor: pointer;
    transition: all 0.3s;
    background: #fff;
    color: #1f1f1f;
}

.promo-button:hover {
    color: #fff;
    background: #1f1f1f;
}
.sign-in-button {
    cursor: pointer;
}

.auth-bar {
    display: flex;
    flex-direction: row;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
}

.user-name {
    color: rgb(51, 51, 51);
}

.sign-in-button,
.log-out-button {
    background: none;
    color: rgb(51, 51, 51);
    border: 1px solid rgb(51, 51, 51);
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s;
}

.sign-in-button:hover,
.log-out-button:hover {
    text-decoration: underline;
}
.footer {
